import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { proceso } from 'src/app/models/proceso.nodel';

@Component({
  selector: 'app-creador',
  templateUrl: './creador.component.html',
  styleUrls: ['./creador.component.css']
})
export class CreadorComponent implements OnInit {

  @Output() procesocreado: EventEmitter<proceso>;
  nuevoProceso: proceso;
  constructor() { 
    this.nuevoProceso = new proceso();
    this.procesocreado = new EventEmitter();
  }

  ngOnInit(): void {
  }
  onClick(){
    this.procesocreado.emit(this.nuevoProceso);
    this.nuevoProceso = new proceso();
  }

}
