export class proceso{
    titulo:string;
    tllegada:number;
    tduracion:number;
    tejecucion:number;
    tentroP:number;
    tterminaP:number;
    entrop:boolean;
    finp:boolean;
    
    constructor(ptitulo=''){
        this.titulo =ptitulo;
        this.tllegada = 0;
        this.tduracion = 0;
        this.tentroP=0;
        this.tterminaP=0;
        this.entrop = true;
        this.finp = true;
        this.tejecucion=0;
    }
}